.card-list {
	-ms-flex-align: center;
	    align-items: center;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	height: 100%;
	width: 100%;
}

.card-list-container {
	-ms-flex-align: center;
	    align-items: center;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	height: 100%;
	overflow: scroll;
	overflow-x: hidden;
	width: 100%;
}

.card-preview {
	border: 1px solid lightgray;
	border-radius: 3px;
	color: black;
	font-size: 14px;
	min-height: 40px;
	margin-bottom: 8px;
	padding: 10px 8px 8px;
	width: 85%;
}



