.action-button-margin {
	margin: 4px;
}

.action-button-container {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-negative: 0;
	    flex-shrink: 0;
	height: 48px;
	margin-bottom: 12px;
}

.status-button {
	min-height: 40px;
	min-width: 110px;
}

.status-dropdown-container {
	background: white;
	border: 1px solid lightgray;
	border-radius: 3px;
	font-size: 14px;
	width: 120px;
}

.status-dropdown-item {
	padding: 4px 16px;
	width: 100%;
}

.status-dropdown-item:hover {
	background: lightgray;
}



