.flashcard-container {
	-ms-flex-align: center;
	    align-items: center;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	width: 100%;
}

.flashcard-content-container {
	border: 1px solid lightgray;
	border-radius: 6px;
	display: -ms-flexbox;
	display: flex;
	height: 340px;
	margin-bottom: 12px;
	overflow: scroll;
	width: 85%;
}

.flashcard-content {
	margin: auto;
	overflow-x: hidden;
	padding: 12px;
	word-wrap: break-word;
}

.editor {
	border: 1px solid #0079bf;
	-webkit-box-shadow: 0 0 2px 0 #0284C6;
	        box-shadow: 0 0 2px 0 #0284C6;
	padding: 12px;
	resize: none;
	width: 100%;
	word-wrap: break-word;
}



